/* Card.css */

.card {
  background-color: #f5f5f5;
  border: 1px solid #203040;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  width: 300px;
  height: 400px;
  margin: 20px;
  text-align: left;
  transition: 0.3s;
}

.card:hover {
  transform: scale(1.05);
}

.card h2 {
  padding: 20px;
  margin: 0;
}

.card p {
  padding: 20px;
  font-size: 14px;
}